.main {
    padding: 1rem 23.4rem 0;

    .wrap {
        .title {
            h2 {
                color: red;
            }
        }
    }

    h1 {
        text-align: center;
        margin-top: 20px;
        margin-bottom: 20px;
        color: #63707e;
        font-size: 1.5rem;
    }

    .row {
        background-color: #f5f6f8;
        padding: 25px 25px;

        .projects {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 2rem;

            .item {
                position: relative;
                overflow: hidden;
                text-align: center;
                border: 3px solid white;
                box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16),
                    0 2px 10px 0 rgba(0, 0, 0, 0.12);

                .img-wrapper {
                    img {
                        transition: 0.3s;

                        &:hover {
                            transform: scale(1.1);
                        }
                    }
                }

                img {
                    width: 100%;
                }

                .description {
                    position: absolute;
                    bottom: -28px;
                    transition: all 0.3s;
                    -webkit-transition: all 0.3s;
                    -moz-transition: all 0.3s;
                    padding: 1rem;
                    margin: 0;
                    color: #fff;
                    background-color: $link-hover;
                    width: 100%;
                }

                &:hover {
                    .description {
                        bottom: 0;
                    }
                }
            }
        }
    }


}