.main-header {
    position: relative;
    background-color: #F5F6F8;
    height: 146px;


    .container {
        width: 80%;
        margin: auto;

        .logo {
            float: left;
            margin-top: 31px;
        }

        .menu {
            float: right;
            margin-top: 61px;

            .menu-nav {

                align-items: left;
                list-style-type: none;

                .nav-item {
                    float: left;
                    display: inline;
                    color: $link-color;
                    font-size: 20px;

                    &:not(:last-child) {
                        margin-right: 15px;
                    }


                }
            }

        }
    }


}