.footer-bottom {
    text-align: center;

    >p {
        padding-bottom: 10px;
        font-size: 12px;
    }

    .triangle {

        margin: auto;
        bottom: 0;
        width: 0;
        height: 0;
        border-left: 40px solid transparent;
        border-right: 40px solid transparent;
        border-bottom: 40px solid #06BC9B;

    }

    .back-to-top {

        position: relative;

        .fa {
            color: #ffffff;
            font-size: 18px;
            position: absolute;
            left: -8px;
            top: 10px;

            &:hover {
                color: #2A3B4E;
                transition: all .3s;
            }

            &.fa-chevron-up {
                visibility: hidden;
            }
        }
    }
}